<template>
  <div class="p-2 ArticleContextBox border border-1">
    <div class="d-flex justify-content-between text-light p-2 h5">
      <div><i class="bi bi-chevron-double-left"></i>上一篇:</div>
      <div>下一篇:<i class="bi bi-chevron-double-right"></i></div>
    </div>
    <v-md-preview
    :text="ArticleContext"
    ref="preview"
    class="ArticleContext  bg-light "
  >
  </v-md-preview>
  </div>
</template>

<script>
export default {
  name: "ArticleContext",
  data() {
    return {};
  },
  updated() {
    this.$nextTick().then(() => {
      this.GetArticleCatalogue();
    });
  },
  computed: {
    ArticleContext() {
      return this.$store.state.index.ArticleContext;
    },
  },
  methods: {
    GetArticleCatalogue() {
      const anchors =
        this.$refs.preview.$el.querySelectorAll("h1,h2,h3,h4,h5,h6");
      const titles = Array.from(anchors).filter(
        (title) => !!title.innerText.trim()
      );
      let Catalagues = [];
      if (!titles.length) {
        Catalagues = [];
        return;
      }

      const hTags = Array.from(
        new Set(titles.map((title) => title.tagName))
      ).sort();

      Catalagues = titles.map((el) => ({
        title: el.innerText,
        lineIndex: el.getAttribute("data-v-md-line"),
        indent: hTags.indexOf(el.tagName),
      }));
      this.$store.commit("index/setCatalaguesData", Catalagues);
    },
  },
};
</script>

<style lang="scss" scoped>
.ArticleContextBox{
  background-color: rgba(48, 54, 105, 0.8);
  .ArticleContext {
  min-height: 1000px;
}
}

</style>
